import React, {Component} from "react";
import {Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem, Jumbotron, FormText, Input, FormGroup} from 'reactstrap';
import {Button, Modal, ModalHeader, ModalBody, Label, Form} from 'reactstrap'; // to made a uncontrolled form
import {NavLink} from 'react-router-dom'

class Header extends Component {
    constructor(props){
        super(props);
        this.state={
            isNavOpen:false, 
            isModalOpen:false
        };
        this.toggleNav = this.toggleNav.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
    }
    toggleNav(){
        this.setState({isNavOpen:!this.state.isNavOpen})
    }
    toggleModal(){
        this.setState({isModalOpen:!this.state.isModalOpen})
    }
    handleLogin(e){
        this.toggleModal();
        alert("username:" +this.username.value + "password: "+ this.password.value+ " remember "+ this.remember.checked);
        e.preventDefault();
    }

    render(){
        return(
            /* REACT FRAGMENT  <></>  or <React.Fragment/>*/ 
            <React.Fragment>
                <Navbar dark expand="md">
                    <div className="container">
                        <NavbarToggler onClick={this.toggleNav} />
                        <NavbarBrand className="mr-auto" href="/">
                            <img src='assets/images/logo.png' height="30" width="30" alt="Ristorante con Fusion"/>
                        </NavbarBrand>
                        <Collapse isOpen={this.state.isNavOpen} navbar>
                            <Nav navbar>
                                <NavItem>
                                    <NavLink className="nav-link" to="/home">
                                        <span className="fa fa-home fa-lg"> Home</span>
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/aboutus">
                                        <span className="fa fa-info fa-lg"> About us</span>
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/menu">
                                        <span className="fa fa-list fa-lg"> Menu</span>
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/contactus">
                                        <span className="fa fa-address-card fa-lg"> Contact us</span>
                                    </NavLink>
                                </NavItem>
                            <Nav className="ml-auto" navbar>
                                <NavItem>
                                    <Button outline onClick ={this.toggleModal}> 
                                        <span className="fa fa-sing-in fa-lg">Login</span>
                                    </Button>
                                </NavItem>
                            </Nav>

                            </Nav>
                        </Collapse>
                    </div>
                </Navbar>
                <Jumbotron>
                    <div className="container">
                        <div className="row row-header">
                            <div className="col-12 col-sm-6">
                                <h1>Ristorante con Fusion</h1>  
                                <p>We take inspiration from the World's best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
                            </div>
                        </div>
                    </div>
                </Jumbotron>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                        Login
                    </ModalHeader>
                    <ModalBody>
                        <Form onSubmit={this.handleLogin}>
                            <FormGroup>
                                <Label htmlFor="username">Username</Label>
                                <Input type="text" id="username" name="username" placeholder="write your user name"
                                    innerRef={(input)=>this.username = input}/>
                            </FormGroup>

                            <FormGroup>
                                <Label htmlFor="password">Password</Label>
                                <Input type="password" id="password" name="password" placeholder="write your password"
                                    innerRef={(input)=>this.password = input}/>
                            </FormGroup>
                            <FormGroup check>
                                <Label check>
                                    <Input type="checkbox" name="remember"
                                        innerRef={(input)=>this.remember = input}/>Remember me
                                </Label>
                            </FormGroup>

                            <Button type="submit" value="submit" className="btn btn-primary">Login</Button>
                        </Form>
                    </ModalBody>
                </Modal>
            </React.Fragment>
        );
    }
}
export default Header;